<script>
import { onMounted, provide } from "vue";
import { useDialog, useSemenSetup } from "@/composable/semen";

import SemenCreateEditDailog from "@/components/semen/create-edit-dialog.vue";
import { usePermissionCheck } from "@/composable/users";

export default {
    components: {
        SemenCreateEditDailog
    },
    setup() {
        const {
            store,
            toast,
            semenList,
            selectedSemen,
            onRowSelect,
            getSemenList,
            deleteSemen,
            openPdfReport
        } = useSemenSetup();

        const { dialog, create, edit, close } = useDialog();
        provide("dialog", dialog);

        const {
            hasUpdatePermission,
            hasDeletePermission,
            hasCreatePermission
        } = usePermissionCheck();

        onMounted(() => {
            if (store.state.artCycle.partner.id) {
                getSemenList(store.state.artCycle.partner.id);
            } else if (store.state.artCycle.patient.sex === "M") {
                console.log("ok");
                getSemenList(store.state.artCycle.patient.id);
            } else {
                console.log("warn");
                toast.add({
                    severity: "warn",
                    detail: "Not Available",
                    life: 3000
                });
            }
        });

        return {
            semenList,
            selectedSemen,
            onRowSelect,
            dialog,
            create,
            edit,
            close,
            deleteSemen,
            openPdfReport,
            hasUpdatePermission,
            hasDeletePermission,
            hasCreatePermission
        };
    }
};
</script>

