export function useSemenSetup() {
    const store = useStore();
    const toast = useToast();
    const semenList = computed(() => store.state.semen.semenList);
    const selectedSemen = ref();

    const onRowSelect = ({ data }) => {
        store.commit("semen/SET_SELECTED_SEMEN", { ...data });
    };

    const getSemenList = (patientId) => {
        emitter.emit("app:show-page-loading", true);
        store
            .dispatch("semen/getSemenList", patientId)
            .then((data) => {
                selectedSemen.value = store.getters["semen/getLastSemen"];
            })
            .catch((error) => {
                const { message } = error;
                toast.add({
                    severity: "warn",
                    summary: "",
                    detail: message,
                    life: 3000
                });
            })
            .finally(() => {
                emitter.emit("app:show-page-loading", false);
            });
    };

    const deleteSemen = () => {
        emitter.emit("app:show-page-loading", true);
        store
            .dispatch("semen/deleteSemen", {
                semenId: store.state.semen.selectedSemen.semenId
            })
            .then((data) => {
                getSemenList(store.state.artCycle.partner.id);
            })
            .catch((error) => {
                const { message } = error;
                toast.add({
                    severity: "error",
                    summary: "",
                    detail: message,
                    life: 3000
                });
                emitter.emit("app:show-page-loading", false);
            });
    };

    const openPdfReport = () => {
        const patient = { ...store.state.artCycle.patient };
        const partner = { ...store.state.artCycle.partner };

        const params = {
            semenId: null,
            partnerName: partner
                ? `${partner.firstName} ${partner.lastName}`
                : null,
            patientName: patient
                ? `${patient.firstName} ${patient.lastName}`
                : null,
            patientDOB: patient
                ? `${patient.year}-${patient.month}-${patient.date}`
                : null,
            partnerDOB: partner
                ? `${partner.year}-${partner.month}-${partner.date}`
                : null,
            reviewed: null,
            performed: null
        };

        if (selectedSemen.value != null) {
            params.semenId = selectedSemen.value.semenId;
        }

        let url = buildPdfUrl("NewSemenReport", params);
        window.open(url);
    };

    return {
        store,
        toast,
        semenList,
        selectedSemen,
        onRowSelect,
        getSemenList,
        deleteSemen,
        openPdfReport
    };
}

export function useDialog() {
    const dialog = reactive({
        type: "Create",
        display: false
    });

    const create = () => {
        dialog.display = true;
        dialog.type = "Create";
    };

    const edit = () => {
        dialog.display = true;
        dialog.type = "Edit";
    };

    const close = () => {
        dialog.display = false;
        dialog.type = "Create";
    };

    return { dialog, create, edit, close };
}

export function useSemenForm() {
    return ref(formData);
}

export function useEmptyFrom() {
    return Object.assign({}, formData);
}

export function useDialogSetup() {
    const store = useStore();
    const dialog = inject("dialog");
    const form = useSemenForm();
    const loading = ref(false);
    const patient = computed(() => store.getters["semen/getPatient"]);
    const partner = computed(() => store.getters["semen/getPartner"]);

    const { getSemenList } = useSemenSetup();

    const onShow = () => {
        loading.value = false;
        if (dialog.type === "Edit") {
            form.value = store.state.semen.selectedSemen;
            console.log(store.state.semen.selectedSemen)
        }
        if (dialog.type === "Create") {
            form.value = useEmptyFrom();
        }
    };

    const create = () => {
        emitter.emit("app:show-page-loading", true);
        loading.value = true;
        store
            .dispatch("semen/createSemen", {
                ...form.value,
                patientId: store.state.artCycle.patient.id,
                partnerId: store.state.artCycle.partner.id,
                semenDate: format(new Date(form.value.semenDate), "yyyy-MM-dd")
            })
            .then(() => {
                dialog.display = false;
                getSemenList(store.state.artCycle.partner.id);
            })
            .finally((error) => {
                dialog.display = false;
                emitter.emit("app:show-page-loading", false);
            });
    };

    const update = () => {
        emitter.emit("app:show-page-loading", true);
        loading.value = true;
        store
            .dispatch("semen/updateSemen", { ...form.value })
            .then(() => {
                dialog.display = false;
                getSemenList(store.state.artCycle.partner.id);
            })
            .catch((error) => {
                console.log(error);
            })
            .finally(() => {
                dialog.display = false;
                emitter.emit("app:show-page-loading", false);
            });
    };

    return {
        patient,
        partner,
        store,
        dialog,
        form,
        loading,
        onShow,
        create,
        update
    };
}
